.footer {
  background-color: $color-bg;
}

.footer__wrapper {
  @include tablets {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 23px;
    padding-bottom: 40px;
  }

  @include tablets {
    padding-top: 16px;
  }

  @include desktop {
    padding-top: 23px;
  }
}

.footer__logo {
  display: flex;
  padding-top: 39px;
  padding-bottom: 27px;
  justify-content: center;
  align-items: center;
  border-bottom: 1px solid $color-border;

  @include tablets {
    border-bottom: none;
    padding-top: 44px;
  }

  @include desktop {
    padding-top: 36px;
  }

  &:hover {
    opacity: 0.8;
  }

  &:active {
    opacity: 0.6;
  }
}

.footer__social {
  padding: 24px 0 12px 0;

  @include tablets {
    padding: 33px 0 20px 0;
  }

  @include desktop {
    padding: 33px 0 28px 0;
  }
}

.footer__copyright {
  border-top: 1px solid $color-border;
  padding-top: 20px;
  padding-bottom: 30px;

  @include tablets {
    border-top: none;
    padding-top: 35px;
  }

  @include desktop {
    padding-top: 36px;
  }
}

.footer__social-list {
  list-style: none;
  margin: 0;
  padding: 0 20px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-content: center;

  @include tablets {
    margin-right: 28px;
    margin-left: 45px;
  }
}

.footer__social-item {
  margin-right: 35px;

  &:last-child {
    margin-right: 3px;
  }

  @include tablets {
    margin-right: 28px;
  }
}

.social__logo--vk {
  width: 23px;
  height: 16px;

  @include tablets {
    margin-top: -1px;
    width: 30px;
    height: 22px;
  }

  @include desktop {
    width: 30px;
    height: 22px;
    margin-top: 2px;
    margin-left: 3px;
  }
}

.social__logo--insta {
  width: 16px;
  height: 16px;

  @include tablets {
    margin-top: -4px;
    width: 23px;
    height: 23px;
  }

  @include desktop {
    margin-top: 0;
    width: 23px;
    height: 23px;
  }
}

.social__logo--fb {
  width: 22px;
  height: 22px;

  @include tablets {
    margin-top: -6px;
    width: 28px;
    height: 33px;
  }

  @include desktop {
    margin-top: -3px;
  }
}

.footer-social__network-link {
  fill: $color-social-link;

  &:hover,
  &:focus {
    fill: $color-button;
  }

  &:active {
    fill: $color-button;
    opacity: 0.3;
  }
}
