  .page-header__toggle::before, .page-header__toggle::after {
    content: "";
    position: absolute;
    width: 100%;
    height: 1.5px;
    background-color: #000000;
    left: 0;
    transition: all 0.3s ease 0s; }
  .page-header__toggle::before {
